*{
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  
}
.App{
  text-align: center;
}

.divTable{
  display: table;
  max-width: 20em;
  float:right;
  display: flex;
  align-items: center;
}
.divTableRow{
  display: table-row;
}
.divTableHeading{
  background-color: #eee;
  display:table-header-group;
  font-weight: bold;
}
.divTableCell,
.divTableHead{
   border: 1px solid #999999;
   display: table-cell;
   padding: 3px 10px;
}
.divTableFoot{
  background-color: #eee;
  display: table-footer-group;
  font-weight: bold;
}
.divTableBody{
  display: table-row-group;  
}
.divTableCell:nth-child(1n){
  background-color: gainsboro;
}
.container{
  display: flex;
  opacity: 0.9;
  background-color: #99aaab;
  justify-content: center;
}

img{
  height:16em;
}
input{
  font-family: sans-serif;
  font-weight: bold;
  box-sizing: border-box;
  font-size: 2rem;
  padding: 1rem;
  display: block;
  margin: 2rem auto;
  background-color: #9485e733;
  padding: 15px;
  display: flex;
  border-radius: 8px;  /* arredonda a borda */
  box-shadow: 1px 3px 8px rgba(0,0,0,0.5);
  border: 0;
    
    color: rgb(17, 16, 16);
    outline: none;  /* tira a linha que fica na hora de digitar algo na caixa */
    
}

body{
  background-image: url("pokedex.gif");
  background-size: 60%;
  background-repeat: no-repeat;
  justify-content: center;
  align-items: center;
  background-color: rgba(241, 236, 236, 0);

}
input::placeholder{
  color: hsla(0, 0%, 0%, 0.856);
  font-weight: bold;
}

@media (max-width: 600px){
  body{
      background-size: 100%;
  }
  input{
    font-size: 28px;
  }
 
}

@media (max-width: 421px){
  body{
    background-size: 100%;
  }
  input{
    font-size: 20px;
  }
  
}